/* Login Page Styles */

.login-page-container {
    min-height: calc(100vh - 80px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background: linear-gradient(135deg, rgba(29, 126, 153, 0.05) 0%, rgba(0, 0, 0, 0.8) 100%);
}

.login-card {
    background: rgba(11, 11, 26, 0.95);
    border: 1px solid rgba(29, 126, 153, 0.2);
    border-radius: 16px;
    padding: 40px;
    width: 100%;
    max-width: 480px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4), 0 0 20px rgba(29, 126, 153, 0.1);
}

.login-header {
    text-align: center;
    margin-bottom: 32px;
}

.login-title {
    font-size: 2rem;
    font-weight: bold;
    color: var(--text-color);
    margin: 0 0 8px 0;
    background: var(--metallic-gradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.login-subtitle {
    color: var(--secondary-text-color);
    font-size: 1rem;
    margin: 0;
}

.login-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.form-field {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.form-label {
    color: var(--text-color);
    font-weight: 500;
    font-size: 0.9rem;
}

/* Form input styles now inherited from components.css */

.password-field-container {
    position: relative;
    display: flex;
    align-items: center;
}

/* Password input styles now inherited from components.css */

.password-toggle-button {
    position: absolute;
    right: 12px;
    background: none;
    border: none;
    color: var(--secondary-text-color);
    cursor: pointer;
    padding: 4px;
    font-size: 1.2rem;
}

.password-toggle-button:hover {
    color: var(--primary-color);
}

.forgot-password-container {
    display: flex;
    justify-content: flex-end;
    margin-top: -8px;
}

.forgot-password-link {
    color: var(--primary-color);
    text-decoration: none;
    font-size: 0.9rem;
}

.forgot-password-link:hover {
    color: var(--secondary-color);
    text-decoration: underline;
}

/* Submit button styles now inherited from components.css as .btn */

.login-footer {
    text-align: center;
    margin-top: 24px;
    padding-top: 24px;
    border-top: 1px solid rgba(29, 126, 153, 0.1);
}

.footer-text {
    color: var(--secondary-text-color);
    margin: 0;
}

.footer-link {
    color: var(--primary-color);
    text-decoration: none;
    font-weight: 500;
}

.footer-link:hover {
    color: var(--secondary-color);
    text-decoration: underline;
}

/* Additional Login Page Inline Styles */

.login-error-message {
    color: #f56565;
    background-color: rgba(245, 101, 101, 0.1);
    border: 1px solid rgba(245, 101, 101, 0.3);
    padding: 12px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
}

.login-success-message {
    color: #48bb78;
    background-color: rgba(72, 187, 120, 0.1);
    border: 1px solid rgba(72, 187, 120, 0.3);
    padding: 12px;
    border-radius: 8px;
    margin-bottom: 20px;
    text-align: center;
}

.google-sign-in-button {
    width: 100%;
    padding: 12px;
    background-color: #14408659;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
}

.google-sign-in-button svg {
    flex-shrink: 0;
    width: 20px;
    height: 20px;
}

.google-sign-in-button:hover {
    background-color: #3367d6;
}

.divider-container {
    display: flex;
    align-items: center;
    margin: 20px 0;
}

.divider-line {
    flex: 1;
    border: none;
    border-top: 1px solid rgba(160, 160, 160, 0.3);
}

.divider-text {
    padding: 0 15px;
    color: var(--text-color);
}

/* Tablet View (768px - 1024px) */
@media screen and (max-width: 1024px) and (min-width: 769px) {
    .login-page-container {
        padding: 15px;
    }

    .login-card {
        max-width: 450px;
        padding: 35px;
    }

    .login-title {
        font-size: 1.8rem;
    }

    .form-input,
    .password-input {
        padding: 11px 14px;
        font-size: 0.95rem;
    }

    .submit-button {
        padding: 13px 22px;
        font-size: 0.95rem;
    }
}

/* Mobile View (481px - 768px) */
@media screen and (max-width: 768px) and (min-width: 481px) {
    .login-page-container {
        padding: 15px;
    }

    .login-card {
        max-width: 400px;
        padding: 30px 25px;
    }

    .login-title {
        font-size: 1.6rem;
    }

    .login-subtitle {
        font-size: 0.95rem;
    }

    .form-input,
    .password-input {
        padding: 10px 14px;
        font-size: 0.9rem;
    }

    .form-label {
        font-size: 0.85rem;
    }

    .submit-button {
        padding: 12px 20px;
        font-size: 0.9rem;
    }

    .password-toggle-button {
        font-size: 1.1rem;
    }

    .forgot-password-link {
        font-size: 0.85rem;
    }
}

/* Small Mobile View (up to 480px) */
@media screen and (max-width: 480px) {
    .login-page-container {
        padding: 10px;
    }

    .login-card {
        max-width: 100%;
        padding: 25px 20px;
        border-radius: 12px;
    }

    .login-title {
        font-size: 1.4rem;
    }

    .login-subtitle {
        font-size: 0.9rem;
    }

    .login-form {
        gap: 16px;
    }

    .form-input,
    .password-input {
        padding: 10px 12px;
        font-size: 0.9rem;
    }

    .form-label {
        font-size: 0.8rem;
    }

    .submit-button {
        padding: 12px 18px;
        font-size: 0.9rem;
    }

    .password-toggle-button {
        font-size: 1rem;
        right: 10px;
    }

    .forgot-password-link {
        font-size: 0.8rem;
    }

    .footer-text {
        font-size: 0.85rem;
    }
}

/* Extra Small Mobile View (up to 360px) */
@media screen and (max-width: 360px) {
    .login-card {
        padding: 20px 15px;
    }

    .login-title {
        font-size: 1.3rem;
    }

    .form-input,
    .password-input {
        padding: 9px 10px;
        font-size: 0.85rem;
    }

    .submit-button {
        padding: 11px 16px;
        font-size: 0.85rem;
    }
}